'use client';

import { useRouter } from 'next/navigation';
import Image from 'next/image';
import Link from 'next/link';
import { Droplets, Leaf, Brain, Trash2, ArrowRight, Users, MapPin, Calendar } from 'lucide-react';
import { technologies, categories } from '@/lib/data';

export default function HomePage() {
  const router = useRouter();

  const categoryData = [
    {
      key: 'decentralized',
      icon: Droplets,
      title: categories.decentralized,
      description: 'Nature-based solutions and decentralized treatment systems',
      color: 'bg-blue-500',
      hoverColor: 'hover:bg-blue-600',
    },
    {
      key: 'stp_improvement',
      icon: Leaf,
      title: categories['stp_improvement'],
      description: 'Advanced technologies for enhancing sewage treatment plants',
      color: 'bg-green-500',
      hoverColor: 'hover:bg-green-600',
    },
    {
      key: 'smart_monitoring',
      icon: Brain,
      title: categories['smart_monitoring'],
      description: 'IoT, AI, and sensor technologies for real-time monitoring',
      color: 'bg-purple-500',
      hoverColor: 'hover:bg-purple-600',
    },
    {
      key: 'sludge_treatment',
      icon: Trash2,
      title: categories['sludge_treatment'],
      description: 'Innovative solutions for sludge and solid waste management',
      color: 'bg-orange-500',
      hoverColor: 'hover:bg-orange-600',
    },
  ];

  const getCategoryCount = (category: string) =>
    technologies.filter((tech) => tech.category === category).length;

  const stats = [
    { label: 'Technologies', value: '45+', icon: Droplets },
    { label: 'Partner Institutes', value: '10+', icon: Users },
    { label: 'Countries', value: '15+', icon: MapPin },
    { label: 'Years of Research', value: '5+', icon: Calendar },
  ];

  return (
    <div className="min-h-screen">
      {/* Hero Section */}
      <section className="relative overflow-hidden">
        {/* Background Image with Overlay */}
        <div className="absolute inset-0 z-0">
          <Image
            src="/slcr18.jpg"
            alt="Clean Rivers Background"
            fill
            className="object-cover"
            priority
          />
          <div className="absolute inset-0 bg-gradient-to-r from-gov-primary/95 via-gov-primary/80 to-gov-secondary/70" />
        </div>

        {/* Hero Content */}
        <div className="relative z-10 max-w-7xl mx-auto px-4 py-20 md:py-32">
          <div className="grid lg:grid-cols-2 gap-12 items-center">
            {/* Left Content */}
            <div className="text-white">
              <div className="inline-flex items-center gap-2 bg-gov-saffron/20 border border-gov-saffron/40 rounded-full px-4 py-1.5 mb-6">
                <span className="w-2 h-2 bg-gov-saffron rounded-full animate-pulse" />
                <span className="text-sm font-medium">Indo-Danish Water Technology Alliance</span>
              </div>

              <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold leading-tight mb-6">
                Smart Laboratory on{' '}
                <span className="text-gov-saffron">Clean Rivers</span>
              </h1>

              <p className="text-lg md:text-xl text-white/90 mb-8 leading-relaxed max-w-xl">
                A comprehensive research initiative showcasing 45+ cutting-edge approaches
                in wastewater treatment — from decentralized solutions to smart monitoring systems.
              </p>

              <div className="flex flex-wrap gap-4">
                <Link
                  href="/technology"
                  className="gov-btn-saffron inline-flex items-center gap-2 text-lg"
                >
                  Explore Technologies
                  <ArrowRight size={20} />
                </Link>
                <Link
                  href="/about/slcr"
                  className="px-6 py-2.5 border-2 border-white text-white font-medium rounded hover:bg-white hover:text-gov-primary transition-all"
                >
                  Learn More
                </Link>
              </div>
            </div>

            {/* Right - Stats Cards */}
            <div className="grid grid-cols-2 gap-4">
              {stats.map((stat, index) => (
                <div
                  key={stat.label}
                  className="bg-white/10 backdrop-blur-md border border-white/20 rounded-xl p-6 text-white hover:bg-white/20 transition-all animate-fade-in-up"
                  style={{ animationDelay: `${index * 100}ms` }}
                >
                  <stat.icon className="w-8 h-8 mb-3 text-gov-saffron" />
                  <div className="text-3xl md:text-4xl font-bold mb-1">{stat.value}</div>
                  <div className="text-sm text-white/70">{stat.label}</div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Wave Divider */}
        <div className="absolute bottom-0 left-0 right-0">
          <svg viewBox="0 0 1440 120" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path
              d="M0 120L60 105C120 90 240 60 360 45C480 30 600 30 720 37.5C840 45 960 60 1080 67.5C1200 75 1320 75 1380 75L1440 75V120H1380C1320 120 1200 120 1080 120C960 120 840 120 720 120C600 120 480 120 360 120C240 120 120 120 60 120H0Z"
              className="fill-white dark:fill-gray-900"
            />
          </svg>
        </div>
      </section>

      {/* Categories Section */}
      <section className="py-20 px-4 bg-white dark:bg-gray-900">
        <div className="max-w-7xl mx-auto">
          <div className="text-center mb-16">
            <h2 className="section-title">Explore by Category</h2>
            <p className="section-subtitle max-w-2xl mx-auto">
              Discover innovative technologies across four major categories of water treatment and management
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {categoryData.map((category) => {
              const Icon = category.icon;
              return (
                <button
                  key={category.key}
                  onClick={() => router.push(`/category/${category.key}`)}
                  className="group card card-hover p-6 text-left transition-all border border-gray-100 dark:border-gray-700"
                >
                  <div className={`w-14 h-14 ${category.color} rounded-xl flex items-center justify-center mb-4 group-hover:scale-110 transition-transform`}>
                    <Icon className="w-7 h-7 text-white" />
                  </div>
                  <h3 className="text-lg font-semibold text-gov-primary dark:text-white mb-2 leading-tight">
                    {category.title}
                  </h3>
                  <p className="text-sm text-gray-600 dark:text-gray-400 mb-4">
                    {category.description}
                  </p>
                  <div className="flex items-center justify-between">
                    <span className="text-sm font-medium text-gov-primary dark:text-gov-saffron">
                      {getCategoryCount(category.key)} Technologies
                    </span>
                    <ArrowRight className="w-4 h-4 text-gray-400 group-hover:text-gov-primary group-hover:translate-x-1 transition-all" />
                  </div>
                </button>
              );
            })}
          </div>
        </div>
      </section>

      {/* About Section */}
      <section className="py-20 px-4 bg-gov-light dark:bg-gray-800">
        <div className="max-w-7xl mx-auto">
          <div className="grid lg:grid-cols-2 gap-12 items-center">
            {/* Image */}
            <div className="relative">
              <Image
                src="/slcr14.jpg"
                alt="SLCR Research"
                width={600}
                height={400}
                className="rounded-2xl shadow-xl object-cover"
              />
              <div className="absolute -bottom-6 -right-6 bg-gov-saffron text-white p-6 rounded-xl shadow-lg hidden md:block">
                <div className="text-3xl font-bold">5+</div>
                <div className="text-sm">Years of Excellence</div>
              </div>
            </div>

            {/* Content */}
            <div>
              <h2 className="section-title">About SLCR Initiative</h2>
              <p className="text-gray-600 dark:text-gray-400 mb-6 leading-relaxed">
                The Smart Laboratory on Clean Rivers (SLCR) is a flagship Indo-Danish collaborative
                initiative aimed at developing and deploying innovative solutions for river rejuvenation
                and sustainable water management.
              </p>
              <p className="text-gray-600 dark:text-gray-400 mb-8 leading-relaxed">
                Working under the Ministry of Jal Shakti and in partnership with Danish institutions,
                SLCR focuses on cutting-edge wastewater treatment technologies, smart monitoring systems,
                and nature-based solutions.
              </p>
              <div className="flex flex-wrap gap-4">
                <Link href="/about/slcr" className="gov-btn inline-flex items-center gap-2">
                  Read More
                  <ArrowRight size={18} />
                </Link>
                <Link href="/projects" className="gov-btn-outline inline-flex items-center gap-2">
                  View Projects
                </Link>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-16 px-4 bg-gov-primary">
        <div className="max-w-4xl mx-auto text-center text-white">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            Join the Clean Rivers Mission
          </h2>
          <p className="text-lg text-white/80 mb-8 max-w-2xl mx-auto">
            Explore our technology forum, connect with partner institutes, or get in touch
            to learn more about our ongoing research and initiatives.
          </p>
          <div className="flex flex-wrap justify-center gap-4">
            <Link
              href="/contact"
              className="gov-btn-saffron inline-flex items-center gap-2"
            >
              Contact Us
              <ArrowRight size={18} />
            </Link>
            <Link
              href="/media"
              className="px-6 py-2.5 border-2 border-white text-white rounded font-medium hover:bg-white hover:text-gov-primary transition-all"
            >
              View Gallery
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
}